<script>
  import axios from "axios";
  import { onMount } from "svelte";
  export let countryCode;

  let countryData = [];
  onMount(() => {
    axios
      .get(
        `https://api.thevirustracker.com/free-api?countryTotal=${countryCode}`
      )
      .then(res => {
        countryData = res.data;
      });
  });
</script>

<style lang="scss">
  .container {
    border: 3px solid lightsalmon;
    border-radius: 5px;
    margin: 5rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1rem 2rem;
    height: 23rem;
    width: 17rem;

    .name {
      font-weight: bold;
      margin-bottom: 1rem;
    }

    .detail {
      margin: 1rem;
      padding: 1rem;
      font-weight: bold;
      font-size: 1.5rem;
    }
  }

  img {
    height: 4rem;
    width: 6rem;
    border: 1px solid lightgrey;
  }
</style>

<div class="container">
  {#if countryData.countrydata}
    <div class="name">{countryData.countrydata[0].info.title}</div>
    <img
      alt={countryCode}
      src={`https://flagpedia.net/data/flags/h160/${countryCode.toLowerCase()}.webp`} />
    <div class="detail">
      <span style="color:#b3b300">
        {countryData.countrydata[0].total_cases}
      </span>
      sick
    </div>
    <div class="detail">

      <span style="color: red">{countryData.countrydata[0].total_deaths}</span>
      deaths
    </div>
    <div class="detail">

      <span style="color:#429028">
        {countryData.countrydata[0].total_recovered}
      </span>
      recovered
    </div>
  {/if}
</div>
